import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import { Downloads } from './components/downloads'
import { Header } from './components/header'
import { Footer } from './components/footer'
import logo from '../assets/icon_256x256.png'
import video from '../assets/rainbow.mp4'
import img from '../assets/rainbow.png'
import favicon from '../assets/favicon.png'
import './styles.css'

const PageHeader = () => {
  return (
    <div id="pageHeader">
      <img id="logo" src={logo} alt="logo" />

      <div id="description">
        <h3>Transee</h3>
        <p>Simple and usefull tool for quick translation</p>

        <Downloads />
      </div>
    </div>
  )
}

const TemplateWrapper = ({ children }) => (
  <div>
    <Helmet
      title="Transee"
      meta={[
        {
          name: 'description',
          content: 'Quick reference to Google translate desktop application',
        },
        {
          name: 'keywords',
          content:
            ' transee, goolge, translate, desktop, app, translation, electron, react, javascript, application, Alessio, Forte, software, developer, javascript, mac, windows, OS',
        },
      ]}
    >
      <link rel="shortcut icon" href={favicon} />
    </Helmet>

    <div id="page">
      <Header />
      <PageHeader />

      <video id="video" src={video} autoPlay loop />
      <img id="img" src={img} />

      <Footer />
    </div>
  </div>
)

TemplateWrapper.propTypes = {
  children: PropTypes.func,
}

export default TemplateWrapper



